import { useQuery, useMutation } from "react-query";
import axios from "./../";

export const useConsultationPatient = ({ params, ...config } = {}) => {
  return useQuery("cp", () => axios.get("cp", { params }), config);
};

export const useListOfThePatientInConsultation = ({
  params,
  ...config
} = {}) => {
  return useQuery(
    "liste-pattient-wating",
    () => axios.get("pdcm", { params }),
    config
  );
};
export const useDetatilOfConsultation = ({ params, ...config } = {}) => {
  return useQuery(
    "Detatil Of Consultation",
    () => axios.get("cd", { params }),
    config
  );
};

export const useCreateConsultation = ({ params, ...config } = {}) => {
  return useMutation((params) => axios.post("consultation", params), config);
};
