import { CloseButton } from "@chakra-ui/close-button";
import { Box, Divider } from "@chakra-ui/layout";
import { useState } from "react";
import { useColorModeValue as mode } from "@chakra-ui/react";
import { TextareaForm } from "./../formInput/Textarea";
import {
  AccordionButton,
  AccordionIcon,
  AccordionItem,
  AccordionPanel,
} from "@chakra-ui/accordion";
import { Input } from "@chakra-ui/input";
import { EditIcon } from "@chakra-ui/icons";
import { MyField } from "../formInput";
import { Prix } from "../formInput/Prix";
export const Examen = (props) => {
  const { id, removeComponentsForm, name } = props;
  const [title, setTitle] = useState("");
  const [showEditTitle, setShowEditTitle] = useState(true);

  return (
    <AccordionItem boxShadow="lg">
      <AccordionButton>
        <Box flex="1" textAlign="left">
          {title != "" ? title : `Examen`}
          <EditIcon
            mx={5}
            onClick={(event) => {
              event.stopPropagation();
              setShowEditTitle(!showEditTitle);
            }}
          />
          <Input
            placeholder="√âcrivez le titre de cet √©l√©ment"
            display={showEditTitle ? `none` : `inline`}
            onChange={(e) => setTitle(e.target.value)}
          />
        </Box>
        <AccordionIcon mx={3} />
        <CloseButton
          onClick={() => removeComponentsForm(id)}
          colorScheme="red"
          bgColor="red.300"
          float="right"
        />
      </AccordionButton>
      <AccordionPanel bgColor={mode("green.50", "gray.700")} pb={4}>
        <MyField
          name={`${name}.type`}
          label="Type d'examen"
          required="Il est requis de compl√©ter ce champ"
        />
        <TextareaForm
          name={`${name}.note`}
          label="Note d'examen"
          required="Il est requis de compl√©ter ce champ"
        />
        <Prix
          name={`${name}.prix`}
          label="Prix d'examen"
          required="Il est requis de compl√©ter ce champ"
        />
      </AccordionPanel>
    </AccordionItem>
  );
};
